module.exports = "\n.navbar {\n  margin: auto;\n  height: auto;\n  position: absulote;\n  border: 1px;\n  overflow: visible;\n  z-index: 9999;\n\n}\n\n#navbarSupportedContent {\n  margin-bottom: 2px;\n  padding: 0;\n}\n\n.navbar-brand{\n  max-height: 150px;\n  max-width: 200px;\n  max-width: 30%;\n  overflow: visible;\n  padding: 0;\n  text-align: center;\n  position: relative;\n\n}\n\n.navbar-nav > li{\n  padding-left:10px;\n  padding-right:10px;\n}\n\nul {\n  list-style: none;\n  display: inline-flex;\n  margin-top: 16px;\n  text-align: center;\n\n}\n\na {\n  text-decoration: none;\n  color: black;\n\n}\n\n.primary-nav-item {\n  list-style: none;\n  display: inline-flex;\n  padding: 12px;\n\n}\n\n.dropbtn , .btn{\n\n  color: black;\n  padding: 12px;\n  font-size: 16px;\n  border: none;\n  cursor: pointer;\n  border-radius: 3px;\n}\n\n/* The container <div> - needed to position the dropdown content */\n\n.dropdown {\n\n  display: flexbox;\n\n}\n\n/* Dropdown Content (Hidden by Default) */\n\n.dropdown-content {\n  display: none;\n  position: absolute;\n  background-color: #f9f9f9;\n  min-width: 160px;\n  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);\n  z-index: 1;\n}\n\n/* Links inside the dropdown */\n\n.dropdown-content a {\n  color: black;\n  position: relative;\n  padding: 12px 16px;\n  text-decoration: none;\n  display: block;\n}\n\n/* Change color of dropdown links on hover */\n\n.dropdown-content a:hover {background-color: #f1f1f1}\n\n/* Show the dropdown menu on hover */\n\n.dropdown:hover .dropdown-content{\n  display: block;\n}\n\n.primary-nav:hover .primary-nav-item {\n  display: block;\n\n}\n\n/* Change the background color of the dropdown button when the dropdown content is shown */\n\n.dropdown:hover .dropbtn {\n  background-color: #81aee2;\n}\n\n.btn:hover {\n  background-color: rgb(193, 230, 193);\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"